import styled from 'styled-components';

const MenuCustomIconSpan = styled.span`
display: block;
position: absolute;
height: 4px;
width: 100%;
background: white;
border-radius: px;
opacity: 1;
left: 0;
`;

const MenuCustomIcon = styled.div`
width: 25px;
height: 16px;
margin: 0 10px 0 0;
position: relative;
& ${MenuCustomIconSpan}:nth-child(1) {
    top: 0px;
    transform: translateY(-50%);
};
& ${MenuCustomIconSpan}:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
};
& ${MenuCustomIconSpan}:nth-child(3) {
    top: 100%;
    transform: translateY(-50%);
};
`;

const MenuWrapper = styled.div`
width: 100%;
background-color: gray;
`;

const MenuTop = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
height: 140px;
background: linear-gradient(150deg, rgb(241,241,241) 50%, white 50%);
`;

const MenuTopLeft = styled.div`

`;

const MenuTopRight = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 5em;
`;

const MenuBottom = styled.div`
background: rgb(0,0,0);
background: linear-gradient(150deg, rgba(10,10,10,1) 85%, rgba(51,0,255,1) 85%);
height: 65px;
`;

const MenuBottomLeft = styled.div`
padding-left: 5em;
display: flex;
flex-direction: row;
align-items: center;
height: inherit;
color: white;
font-size: 24px;
font-weight: bold;
`;

const MenuBottomLeftItem = styled.div`
margin: auto 40px;
display: flex;
align-items: center;

transition: all 100ms ease-in-out;
&:hover {
    color: rgba(51,0,255,1);
    cursor: pointer;
}
`;

const VerticalSeparator = styled.div`
margin: auto 25px;
height: 40px;
width: 1px;
background-color: white;
`;

const CustomArrowSpan = styled.span`
display: block;
position: absolute;
height: 10px;
margin: 1px;
width: 3px;
background: white;
border-radius: px;
opacity: 1;
left: 0;
`;

const CustomArrowIcon = styled.div`
width: 6px;
height: 6px;
margin: 0 0 0 15px;
position: relative;
& ${CustomArrowSpan}:nth-child(1) {
    left: 0%;
    top: 100%;
    transform: translate(-50%, -50%) rotate(-45deg);
};
& ${CustomArrowSpan}:nth-child(2) {
    top: 100%;
    left: 100%;
    transform: translate(-50%, -50%) rotate(45deg);
};
`;

const SocialImage = styled.img`
    width: 32px;
    height: 32px;
    margin: 3px;
`;

const EmailAddress = styled.span`
    font-size: 22px;
    font-weight: bold;
    padding-right: 25px;
`;

const HeadingLogo = styled.img`
padding-left: 7em;
    height: 100px;
`;

// MENU ICON STYLED
const MenuIcon = () => {
    return (
        <MenuCustomIcon>
            <MenuCustomIconSpan></MenuCustomIconSpan>
            <MenuCustomIconSpan></MenuCustomIconSpan>
            <MenuCustomIconSpan></MenuCustomIconSpan>
        </MenuCustomIcon>)
}

const ArrowIcon = () => {
    return (
        <CustomArrowIcon>
            <CustomArrowSpan></CustomArrowSpan>
            <CustomArrowSpan></CustomArrowSpan>
        </CustomArrowIcon>)
}

export {
    MenuIcon,
    ArrowIcon,
    SocialImage,
    EmailAddress,
    HeadingLogo,
    MenuWrapper,
    MenuTop,
    MenuTopLeft,
    MenuTopRight,
    MenuBottom,
    MenuBottomLeft,
    MenuBottomLeftItem,
    VerticalSeparator
}