import styled from 'styled-components';

const ContentRight = styled.div`
width: calc(25% - 10px);
min-height: 200px;
margin: 5px;
`;

const SocialMediaBoxWrapper = styled.div`
width: calc(33% - 5px);
height: 100px;
background-color: ${props => props.color};
display: flex;
justify-content: center;
align-items: center;

svg {
    transition: all 200ms ease-in-out;
}
&:hover {
    cursor: pointer;
    svg {
        transform: scale(1.4, 1.4);
    };
}
`;

const IconWrapper = styled.div`
`;

const SocialMediaChooseButton = styled.div`
width: 55px;
height: 20px;
background-color: rgb(51,0,255);
color: white;
padding: 5px 10px;
font-size: 11px;
font-weight: bold;
display: flex;
justify-content: center;
align-items: center;
&:hover {
    cursor: pointer;
    background-color: black;
}
`;

const CategoryWrapper = styled.div`
font-size: 16px;
font-weight: bold;
margin: 15px 25px;
display: flex;
flex-direction: column;

transition: all 100ms ease-in-out;
&:hover {
    cursor: pointer;
    color: rgba(51,0,255,1);
}
`;

const CategorySeparator = styled.div`
width: 100%;
margin: 10px auto;
height: 1px;
background-color: rgb(220,220,220);
`;


const SocialMediaBox = ({customColor, icon}) => {
    return (
        <SocialMediaBoxWrapper color={customColor}>
            <IconWrapper>{icon}</IconWrapper>
        </SocialMediaBoxWrapper>
    )
}

const SocialMediaButton = ({title}) => {
    return (
        <SocialMediaChooseButton>{title}</SocialMediaChooseButton>
    )
}

const Category = ({bottomSeparator, title}) => {
    return (
        <div>
        <CategoryWrapper>
            {title}
        </CategoryWrapper>
        {bottomSeparator && <CategorySeparator />}
        </div>
    )
}
export {
    ContentRight,
    SocialMediaBox,
    SocialMediaButton,
    Category
}