import React from 'react';
import Menu from '../components/menu';
import Trending from '../components/trending';
import BlogLeft from '../components/blog_left';
import BlogRight from '../components/blog_right';
import Footer from '../components/footer';
import styled from 'styled-components';


const HomePageWrapper = styled.div`
font-family: 'Montserrat', sans-serif;
`;

const MenuWrapper = styled.div`
`;

const ContentWrapper = styled.div`
margin: 25px 10rem;
`;

const ContentTwoSectionSeparator = styled.div`
display: flex;
`;

const FooterWrapper = styled.div`
position: relative;
`;

const HomePage = () => {
    return (
        <HomePageWrapper>
            <MenuWrapper>
                <Menu />
            </MenuWrapper>
            <ContentWrapper>
                <Trending />
                <ContentTwoSectionSeparator>
                    <BlogLeft></BlogLeft>
                    <BlogRight></BlogRight>
                </ContentTwoSectionSeparator>
            </ContentWrapper>
            <FooterWrapper>
                <Footer />
            </FooterWrapper>
        </HomePageWrapper>
    )
}

export default HomePage;