import styled from 'styled-components';

const BoxAd = styled.div`
width: 100%;
height: 350px;
background: url(${props => props.customAdUrl || "rgba(0,0,0,0.3)"});
background-position: center;
background-size: cover;
background-repeat: no-repeat;
margin: 20px 0;
`;

export {
    BoxAd
}