import React from 'react';
import Twitter from '../icons/twitter.png';
import Instagram from '../icons/instagram.png';
import Facebook from '../icons/facebook.png';
import Logo from '../icons/topinflu.png';

import {
    MenuIcon,
    MenuWrapper,
    MenuTop,
    MenuTopRight,
    MenuTopLeft,
    MenuBottom,
    MenuBottomLeft,
    MenuBottomLeftItem,
    VerticalSeparator,
    ArrowIcon,
    SocialImage,
    EmailAddress,
    HeadingLogo
} from '../styles/menu_styled';

const Menu = () => {

    return (
    <MenuWrapper>
        <MenuTop>
            <MenuTopLeft>
                <HeadingLogo src={Logo}/>
            </MenuTopLeft>
            <MenuTopRight>
                <EmailAddress>kontakt@topinflu.pl</EmailAddress>
                <SocialImage src={Twitter}></SocialImage>
                <SocialImage src={Instagram}></SocialImage>
                <SocialImage src={Facebook}></SocialImage>
            </MenuTopRight>
        </MenuTop>
        <MenuBottom>
            <MenuBottomLeft>
                <MenuBottomLeftItem>
                    <MenuIcon />
                    HOME
                </MenuBottomLeftItem>
                <VerticalSeparator />
                <MenuBottomLeftItem>YouTube<ArrowIcon /></MenuBottomLeftItem>
                <MenuBottomLeftItem>Instagram<ArrowIcon /></MenuBottomLeftItem>
                <MenuBottomLeftItem>TikTok<ArrowIcon /></MenuBottomLeftItem>
                <MenuBottomLeftItem>Twitter<ArrowIcon /></MenuBottomLeftItem>
                <MenuBottomLeftItem>Esport<ArrowIcon /></MenuBottomLeftItem>
            </MenuBottomLeft>
        </MenuBottom>    
    </MenuWrapper>
    )
}

export default Menu;