import styled from 'styled-components';

const TrendingWrapper = styled.div`
    width: 100%;
`;

const TrendingHeaderText = styled.div`
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    padding: 0 5px;
    display: flex;
    height: 21px;
    align-items: center;
`;

const TrendingHeaderConst = styled.div`
    font-weight: bold;
`;

const CustomArrowSpan = styled.span`
position: absolute;
height: 8px;
margin: 1px;
width: 2px;
background: black;
border-radius: px;
opacity: 1;
left: 0;
`;

const CustomArrowIcon = styled.div`
width: 20px;
height: 15px;
margin: 0 15px 0 15px;
position: relative;
& ${CustomArrowSpan}:nth-child(1) {
    left: 50%;
    bottom: 35%;
    transform: rotate(-45deg);
};
& ${CustomArrowSpan}:nth-child(2) {
    left: 50%;
    top: 35%;
    transform: rotate(45deg);
};
`;

const TrendingContent = styled.div`
    margin: 1rem 0;
    height: 38rem;

    display: flex;
    flex-flow: column wrap;
    align-items: center;
`;

const RightArrowIcon = () => {
    return (
        <CustomArrowIcon>
            <CustomArrowSpan></CustomArrowSpan>
            <CustomArrowSpan></CustomArrowSpan>
        </CustomArrowIcon>)
}

const TrendingHeader = ({title}) => {
    return (
        <TrendingHeaderText>
            <TrendingHeaderConst>NA CZASIE</TrendingHeaderConst>
            <RightArrowIcon />
            {title}
        </TrendingHeaderText>
    )
}

export {
    TrendingWrapper,
    TrendingHeader,
    TrendingContent
}