import styled from 'styled-components';
import { keyframes } from 'styled-components';
import Moment from 'moment';
import CalendarSVG from '../icons/calendar';

const HorizontalSeparatorFW = styled.div`
width: 75%;
margin: 10px auto;
height: 1px;
background-color: rgb(220,220,220);
`;

const SectionInfo = styled.div`
position: absolute;
top: 0;
left: 0;
height: 30px;
min-width: 50px;
background: linear-gradient(90deg, rgba(51,0,255,1) 10px, black 10px);

display: flex;
padding-right: 10px;
padding-left: 25px;
align-items: center;
color: white;
font-weight: bold;
transition: background 2s ease-in-out;
&:hover {
    cursor: pointer;
    background: linear-gradient(90deg, rgba(51,0,255,1) 100%, black 10px);
}
transition: all 100ms ease-in-out;
`;

const SectionTitle = styled.div`
position: absolute;
width: calc(100% - 30px);
height: 100px;
max-height: 100px;
background-color: rgba(0,0,0, 0.8);
bottom: 0;
display: flex;
align-items: center;
padding-left: 30px;
font-size: ${props => props.customFontSize || "20px"};
font-weight: bold;
color: white;
transition: all 100ms ease-in-out;
`;

const SectionDescription = styled.div`
position: absolute;
top: 100%;
width: calc(100% - 50px);
height: calc(100% - 100px);
background-color: rgba(0,0,0,0.8);
padding: 25px;
color: white;
font-size: ${props => props.customFontSize || "14px"};
font-weight: bold;
overflow: hidden;
transition: all 100ms ease-in-out;
`;

const BigPanelWrapper = styled.div`
position: relative;
display: block;
width: calc(50% - 10px);
margin: 5px;
height: calc(100% - 10px);
background: url(${props => props.imageSrc || "rgba(0,0,0,0.3)"});
background-position: center;
background-size: cover;
background-repeat: no-repeat;
overflow: hidden;

&:hover {
    cursor: pointer;
    & ${SectionInfo} {
        opacity: 0;
    }
    & ${SectionTitle} {
        bottom: calc(100% - 100px);
    }
    & ${SectionDescription} {
        top: 100px;
    }
}
`;

const MediumPanelWrapper = styled.div`
position: relative;
display: block;
box-sizing: border-box;
width: calc(25% - 10px);
margin: 5px;
height: calc(100% - 10px);
background: url(${props => props.imageSrc || "rgba(0,0,0,0.3)"});
background-position: center;
background-size: cover;
background-repeat: no-repeat;
overflow: hidden;

&:hover {
    cursor: pointer;
    & ${SectionInfo} {
        opacity: 0;
    }
    & ${SectionTitle} {
        bottom: calc(100% - 100px);
    }
    & ${SectionDescription} {
        top: 100px;;
    }
}
`;

const SmallPanelWrapper = styled.div`
position: relative;
display: block;
box-sizing: border-box;
width: calc(25% - 10px);
margin: 5px;
height: calc(50% - 10px);
background: url(${props => props.imageSrc || "rgba(0,0,0,0.3)"});
background-position: center;
background-size: cover;
background-repeat: no-repeat;
overflow: hidden;

&:hover {
    cursor: pointer;
    & ${SectionInfo} {
        opacity: 0;
    }
    & ${SectionTitle} {
        bottom: calc(100% - 100px);
    }
    & ${SectionDescription} {
        top: 100px;;
    }
}
`;

const RibbonWrapper = styled.div`
position: relative;
display: flex;
width: 100%;
height: 40px;
background-color: white;
background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAICAYAAADED76LAAAAPklEQVQoU2P89OnTfwYc4ODBgwyMuBSAJO3t7bErgEmCDMYwAVkSQwG6JIoCbJJwBbgkwQo2b978H+RaXAAA0BYy9KbAPVQAAAAASUVORK5CYII=);
overflow: hidden;
`;

const RibbonLeft = styled.div`
height: 100%;
background-color: ${props => props.color || "rgba(51,0,255,1)"};

display: inline-block;
font-weight: bold;
font-size: 20px;
padding: 0 25px;
line-height: 40px;
color: white;
z-index: 1;
`;

const Marquee = keyframes`
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(-100%);
    }
`;

const RibbonMarquee = styled.div`
height: 100%;
font-weight: bold;
min-width: 100%;
text-align: center;
padding: 0 25px;
line-height: 40px;
color: black;
transform-origin: center;
animation: ${Marquee} 20s linear infinite;
animation-play-state: running;
white-space: nowrap;
overflow: hidden;
`;

const ExtraSmallPanelOuterDesc = styled.div`
position: absolute;
width: calc(100% - 15px);
height: calc(100% - 15px);
background: rgba(0,0,0,0.8);
margin-left: 100%;
color: white;
padding: 15px;
font-size: 12px;
transition: margin-left 150ms ease-in-out;
`;

const ExtraSmallPanelOuterWrapper = styled.div`
position: relative;
display: flex;
height: 100px;
width: calc(100% -5px);
margin: 0 5px;
justify-content: space-between;
overflow: hidden;

&:hover {
    cursor: pointer;
    & ${ExtraSmallPanelOuterDesc} {
        margin-left: 0;
    }
}
`;

const ExtraSmallPanelOuterImage = styled.div`
min-width: 150px;
height: 100px;
background: url(${props => props.imageSrc || "rgba(0,0,0,0.3)"});
background-position: center;
background-size: cover;
background-repeat: no-repeat;
overflow: hidden;
`;

const ExtraSmallPanelOuterRightWrapper = styled.div`
display: flex;
justify-content: center;
flex-direction: column;
margin: 0 15px;
`;

const ExtraSmallPanelOuterTitle = styled.div`
font-size: ${props => props.customFontSize || "18px"};
font-weight: bold;
margin-bottom: 15px;
`;

const ExtraSmallPanelOuterDate = styled.div`
display: flex;
align-items: center;
justify-content: space-between;
font-size: ${props => props.customFontSize || "16px"};

`;

const BigPanelEmbedInfo = ({section, title, image, description}) => {
    return (
        <BigPanelWrapper imageSrc={image}>
            <SectionInfo>{section}</SectionInfo>
            <SectionTitle customFontSize="26px">{title}</SectionTitle>
            <SectionDescription customFontSize="20px">{description}</SectionDescription>
        </BigPanelWrapper>
    )
}

const MediumPanelEmbedInfo = ({section, title, image, description}) => {
    return (
        <MediumPanelWrapper imageSrc={image}>
            <SectionInfo>{section}</SectionInfo>
            <SectionTitle customFontSize="20px">{title}</SectionTitle>
            <SectionDescription customFontSize="14px">{description}</SectionDescription>
        </MediumPanelWrapper>
    )
}

const SmallPanelEmbedInfo = ({section, title, image, description}) => {
    return (
        <SmallPanelWrapper imageSrc={image}>
            <SectionInfo>{section}</SectionInfo>
            <SectionTitle customFontSize="20px">{title}</SectionTitle>
            <SectionDescription customFontSize="11px">{description}</SectionDescription>
        </SmallPanelWrapper>
    )
}

const RibbonInfoHeader = ({title, news, customColor}) => {
    return (
        <RibbonWrapper>
            <RibbonLeft color={customColor}>{title}</RibbonLeft>
            <RibbonMarquee>{news}</RibbonMarquee>
        </RibbonWrapper>
    )
}

const SmallPanelOuterInfo = ({section, title, image, description, separator = true}) => {
    return (
        <>
        <ExtraSmallPanelOuterWrapper>
            <ExtraSmallPanelOuterImage imageSrc={image}/>
            <ExtraSmallPanelOuterRightWrapper>
                <ExtraSmallPanelOuterTitle customFontSize="16px">{title}</ExtraSmallPanelOuterTitle>
                <ExtraSmallPanelOuterDate><CalendarSVG />{Moment().format("LL")}</ExtraSmallPanelOuterDate>
            </ExtraSmallPanelOuterRightWrapper>
            <ExtraSmallPanelOuterDesc>{description}</ExtraSmallPanelOuterDesc>
        </ExtraSmallPanelOuterWrapper>
        
        {separator && <HorizontalSeparatorFW />}
        </>
    )
}

export {
    BigPanelEmbedInfo,
    MediumPanelEmbedInfo,
    SmallPanelEmbedInfo,
    RibbonInfoHeader,
    SmallPanelOuterInfo
}